                                            '__cdata': ' Sk jobber innenfor normal arbeidstid. P ettermiddagen har du fri og kan gjre andre aktiviteter.<br/>\n                        <br/> Hvis du har lett for  snu dgnet, kan det fre til drlige rutiner. Oppsk jobbklubber og lignende som hjelper deg med  st opp til vanlig tid.',
