                                        '__cdata': ' Last ned apper som <a href="http://no.indeed.com/" target="_blank">Indeed</a>, <a href="https://jyb.no/">Jyb</a> og InstaCruit<span style="font-family: \'Times New Roman\',\'serif\'; font-size: 12pt; mso-fareast-font-family: \'Times New Roman\'; mso-ansi-language: NO-BOK; mso-fareast-language: NO-BOK; mso-bidi-language: AR-SA;">.  </span>\n                     \n                      Hvis du ser etter jobb i et annet land i Europa, kan appen <a href="https://play.google.com/store/apps/details?id=eu.europa.ec.dgempl.eures.android.euresjobs"\n                           target="_blank">EURES - din jobb i Europa</a> vre noe for deg.',
