                                        '__cdata': ' Det kan vre lettere  stikke innom en butikk eller produksjonsbedrift enn virksomheter med lengre vei opp til toppen. Men det er ingen fasit, du m bruke skjnn. Finn ut p forhnd hvem i bedriften som er daglig leder eller hvem som har ansvar for ansettelser. Forsk  f en kort prat.\n                     <ul \n                        <li>Vr hflig, positiv og smil </li>\n                        <li>Forklar hvorfor du har kommet og spr om  f snakke med daglig leder</li>\n                        <li>Spr om de har noen ledige jobber eller eventuelt om nr det kan bli noe ledig neste gang</li>\n                        <li>Forhr deg litt om arbeidsoppgaver og arbeidstider</li>\n                        <li>Gi fra deg en pen sknad eller en tilpasset CV, og si at du gjerne mter til intervju</li>\n                     </ul>\n                      P <a href="https://cvnerden.no/stor-samling-tips/" target="_blank">cvnerden.no</a> finner du noen tips til hva du br gjre nr du leverer sknaden personlig.',
